//Claim
const totalBirdsNestedRef = ref(0);
const maxBirdsUpgradableRef = ref(0);
const maxAvailableRef = ref(0);
const claimBirdsRef = ref(0);
const claimBirds3Ref = ref(0);
const claimBirds5Ref = ref(0);
const claimMaxBirdsRef = ref(0);
const claimMaxBirds3Ref = ref(0);
const claimMaxBirds5Ref = ref(0);
const claimsAvailableRef = ref(0);
const claimSelectedRef = ref(0);
const showClaimUpgradeRef = ref(false);


const claimUpgradeAmount = ref(0);


function handleUpdateClaim(amount) {       
    claimBirdsRef.value = amount;
    claimsAvailableRef.value = totalBirdsNestedRef.value - (claimBirdsRef.value+claimBirds3Ref.value*3+claimBirds5Ref.value*5);
    updateClaims(claimsAvailableRef.value); 
}

function handleUpdateClaim3(amount) {    
    claimBirds3Ref.value = amount;
    claimsAvailableRef.value = totalBirdsNestedRef.value - (claimBirdsRef.value+claimBirds3Ref.value*3+claimBirds5Ref.value*5);
    updateClaims(claimsAvailableRef.value); 
}

function handleUpdateClaim5(amount) {   
    claimBirds5Ref.value = amount;
    claimsAvailableRef.value = totalBirdsNestedRef.value - (claimBirdsRef.value+claimBirds3Ref.value*3+claimBirds5Ref.value*5);
    updateClaims(claimsAvailableRef.value); 
}

function updateClaims(claims) {
    if(totalBirdsNestedRef.value > maxBirdsUpgradableRef.value) {
        maxAvailableRef.value = maxBirdsUpgradableRef.value - (claimBirdsRef.value+claimBirds3Ref.value*3+claimBirds5Ref.value*5);
        claimMaxBirdsRef.value = claimBirdsRef.value + maxAvailableRef.value;
        claimMaxBirds3Ref.value = claimBirds3Ref.value+Math.floor(maxAvailableRef.value/3);
        claimMaxBirds5Ref.value = Math.min(claimBirds5Ref.value+Math.floor(maxAvailableRef.value/5),maxC5Claims);
    }       
    else {
        claimMaxBirdsRef.value = claimBirdsRef.value + claims;
        claimMaxBirds3Ref.value = claimBirds3Ref.value+Math.floor(claims/3);
        claimMaxBirds5Ref.value = Math.min(claimBirds5Ref.value+Math.floor(claims/5),maxC5Claims);

        
    } 
}

function claimsSelectedOutput(claim, claim3, claim5) {
    claimSelectedRef.value = claim+claim3*3+claim5*5;
    return claimSelectedRef.value;
}

async function claimSelection() {
  
    if(!selectedAccount.value) {
        await onConnect();
    }
    

    if(claimBirdsRef.value > 0) {
        showClaimUpgradeRef.value = true;
        claimUpgradeAmount.value = claimBirdsRef.value;
    } else {
      

      let total = claimBirds3Ref.value*3 + claimBirds5Ref.value*5;
      if(total > 0) {
        let claiming = [...birdsClaimableRef.value];
        claiming.length = total;
        console.log(claiming);

        await contractBox.methods.claimTypes(claiming, 0, claimBirds3Ref.value, claimBirds5Ref.value).send({from: selectedAccount.value});
        window.$message.success("Boxes claimed!");
        claimBirdsRef.value = 0;
        claimBirds3Ref.value = 0;
        claimBirds5Ref.value = 0;
        claimSelectedRef.value = 0;
        showClaimUpgradeRef.value = false;
        await fetchAccountData();
      } else {
        window.$message.error("No box selected");
        
      }
    }
}

async function claimUpgrade() {
  let totalmulti = claimBirds3Ref.value*3 + claimBirds5Ref.value*5;
  if(totalmulti == 0) {
    let claiming = [...birdsClaimableRef.value];
    claiming.length = claimBirdsRef.value;
    await contractBox.methods.claim(claiming).send({from: selectedAccount.value,value: (15000000000000000*parseInt(claimUpgradeAmount.value))});
    window.$message.success("Success!");
    claimBirdsRef.value = 0;
    claimBirds3Ref.value = 0;
    claimBirds5Ref.value = 0;
    claimSelectedRef.value = 0;
    showClaimUpgradeRef.value = false;
    await fetchAccountData();
  } else {
    let total = claimBirdsRef.value + totalmulti;
    let claiming = [...birdsClaimableRef.value];
    claiming.length = total;
    await contractBox.methods.claimTypes(claiming, claimBirdsRef.value, claimBirds3Ref.value, claimBirds5Ref.value).send({from: selectedAccount.value,value: (15000000000000000*parseInt(claimUpgradeAmount.value))});
    window.$message.success("Success!");
    claimBirdsRef.value = 0;
    claimBirds3Ref.value = 0;
    claimBirds5Ref.value = 0;
    claimSelectedRef.value = 0;
    showClaimUpgradeRef.value = false;
    await fetchAccountData();
  }


}

async function claimNoUpgrade() {
  let totalmulti = claimBirds3Ref.value*3 + claimBirds5Ref.value*5;
  if(totalmulti == 0) {
    let claiming = [...birdsClaimableRef.value];
    claiming.length = claimBirdsRef.value;
    await contractBox.methods.claim(claiming).send({from: selectedAccount.value});
    window.$message.success("Success!");
    claimBirdsRef.value = 0;
    claimBirds3Ref.value = 0;
    claimBirds5Ref.value = 0;
    claimSelectedRef.value = 0;
    showClaimUpgradeRef.value = false;
    await fetchAccountData();
  } else {
    let total = claimBirdsRef.value + totalmulti;
    let claiming = [...birdsClaimableRef.value];
    claiming.length = total;
    await contractBox.methods.claimTypes(claiming, claimBirdsRef.value, claimBirds3Ref.value, claimBirds5Ref.value).send({from: selectedAccount.value});
    window.$message.success("Success!");
    claimBirdsRef.value = 0;
    claimBirds3Ref.value = 0;
    claimBirds5Ref.value = 0;
    claimSelectedRef.value = 0;
    showClaimUpgradeRef.value = false;
    await fetchAccountData();
  }


}



// Upgrade
const upgradesTotalAvailableRef = ref(0);
const upgradesAvailableRef = ref(0);
const valueUpgradesMaxRef = ref(0);
const upgradeSelectionRef = ref(0);
const keySelectedRef = ref(null);
const indexSelectedRef = ref(null);

const rangeUpdateBoxesRef = ref([]);  
const selectBorderedRef = ref([]);
const selectBorderedIndexRef = ref([]);
const boxNameRef = ref(['SOUL BOX', 'SPIRIT BOX', 'MACHINA BOX', 'OMEN BOX', 'AIR BOX', 'WATER BOX']);

const valueUpgradesRef = ref([]);


export const storeConnect = reactive({
    astrConnectButton: astrConnectButtonRef,
    tooltipConnectButton: tooltipConnectButtonRef,
    selectedAccount,
    claimUpgradeAmount,
    claimNoUpgrade,
    claimUpgrade,
    minted3UI,
    minted5UI,
    publicMaxSupply,
    publicMinted,
    publicPrice,
    
    onWalletBtn,
    handleUpdateAmount,
    mintNft,
    mintAmount: mintAmountRef,
    price: publicPrice,
    totalSupply: totalSupplyRef,
    disabledBtnConnect: disabledBtnConnectRef,
    pricePerOne: computed(() => {
        return pricePerOneOutput(publicPrice.value)
    }),
    priceTotal: computed(() => {
        return priceTotalOutput(publicPrice.value, mintAmountRef.value)
    }),
    totalDeadlineMintClaim,
    // Claim
    handleUpdateClaim,
    handleUpdateClaim3,
    handleUpdateClaim5,
    claimSelection,
    totalBirdsNested: totalBirdsNestedRef,
    maxBirdsUpgradable: maxBirdsUpgradableRef,
    claimBirds: claimBirdsRef,
    claimBirds3: claimBirds3Ref,
    claimBirds5: claimBirds5Ref,
    claimMaxBirds: claimMaxBirdsRef,
    claimMaxBirds3: claimMaxBirds3Ref,
    claimMaxBirds5: claimMaxBirds5Ref,
    claimsAvailable: claimsAvailableRef,
    claimsSelected: computed(() => {
        return claimsSelectedOutput(claimBirdsRef.value, claimBirds3Ref.value, claimBirds5Ref.value)
    }),
    showClaimUpgrade: showClaimUpgradeRef,
    // Uprade
    upgradesAvailable: upgradesAvailableRef,
    upgradeSelection: upgradeSelectionRef,
    valueUpgradesMax: valueUpgradesMaxRef,
    selectBoxToUpgrade(key, index) {
      // geselect
      for (let i=0; i<rangeUpdateBoxesRef.value.length; i++) {
        selectBorderedRef.value[i] = false;
      }
      selectBorderedIndexRef.value[index] = index;
      keySelectedRef.value = key;
      indexSelectedRef.value = parseInt(index);

      selectBorderedRef.value[index] = !selectBorderedRef.value[index];
    },
    selectBordered: selectBorderedRef,
    selectBorderedIndex: selectBorderedIndexRef,
    rangeUpdateBoxes: rangeUpdateBoxesRef,
    handlePlusButtonClick() {
      if(indexSelectedRef.value >= 0 && upgradesAvailableRef.value != 0) {
        switch(keySelectedRef.value) {
            case 0:
                valueUpgradesMaxRef.value = 5;
            break;
            case 1:
                valueUpgradesMaxRef.value = 4;
            break;
            case 2:
                valueUpgradesMaxRef.value = 3;
            break;
            case 3:
                valueUpgradesMaxRef.value = 2;
            break;
            case 4:
                valueUpgradesMaxRef.value = 1;
            break;
            default:
                valueUpgradesMaxRef.value = 0;
        }

        if(valueUpgradesRef.value[indexSelectedRef.value] < valueUpgradesMaxRef.value) {
            upgradesAvailableRef.value = Math.max(0, upgradesAvailableRef.value - 1);
        }
        valueUpgradesRef.value[indexSelectedRef.value] = Math.min(valueUpgradesMaxRef.value, valueUpgradesRef.value[indexSelectedRef.value] + 1);                  
      }         
    },
    handleMinusButtonClick() {
      if(indexSelectedRef.value >= 0) {
        if(valueUpgradesRef.value[indexSelectedRef.value] > 0) {
            upgradesAvailableRef.value = Math.min(upgradesTotalAvailableRef.value, upgradesAvailableRef.value + 1);
        }
        valueUpgradesRef.value[indexSelectedRef.value] = Math.max(0, valueUpgradesRef.value[indexSelectedRef.value] - 1);       
      }
    },
    indexSelected: indexSelectedRef,
    boxName: boxNameRef,
    valueUpgrades: valueUpgradesRef,
});

